.logo {
  &--link {
    &:hover {
      opacity: 0.8;
    }

    &:focus,
    &:active {
      opacity: 0.6;
      border: none;
      outline: none;
    }
  }
}

.logo__image {
  width: 100%;
  height: auto;
}

.logo__slogan {
  @media (min-width: $media-tablet) {
    width: 142px;
    height: auto;
  }
}
